<template>
    <div id = "PairRecord">
        <li>This page shows the distance of each comparison field</li>

<!--              <table>-->
<!--                    <tr>-->
<!--                        <th>Record_Pair</th>-->
<!--                        <th>Record1_ID</th>-->
<!--                        <th>Record2_ID</th>-->
<!--                        <th>Link_Status</th>-->
<!--                    </tr>-->

<!--                    <tr v-for="i in filterData" :key="i.record1_id">-->
<!--                        <td v-text="i.RecordPair">-->
<!--                        <td v-text="i.record1_id">-->
<!--                        <td v-text="i.record2_id">-->
<!--                        <td v-text="i.LinkStatus">-->

<!--                        </tr>-->
<!--                </table>-->

        <el-table :data="filterData">
            <el-table-column prop="RecordPair" label="RecordPair"></el-table-column>
            <el-table-column prop="record1_id" label="record1_id"></el-table-column>
            <el-table-column prop="record2_id" label="record2_id"></el-table-column>
            <el-table-column prop="field_value1" label="field_value1"></el-table-column>
            <el-table-column prop="field_value2" label="field_value2"></el-table-column>
            <el-table-column prop="metric_name" label="metric_name"></el-table-column>
            <el-table-column prop="pair_distance" label="pair_distance">
                <template slot-scope="scope">
                    {{scope.row.pair_distance}}
                    <el-progress
                            type="line"
                            :text-inside="true"
                            :format="format"
                            :percentage="scope.row.pair_distance*100"
                            :stroke-width="20"
                            :color="customColorMethod(scope.row.pair_distance*100)"
                    ></el-progress>

                </template>
            </el-table-column>


<!--        </el-table>-->
        </el-table>
    </div>
</template>

<script src="https://cdn.jsdelivr.net/npm/axios/dist/axios.min.js"></script>
<script>
    export default{
    name: "PairRecord",
    data(){
        return {

        record1_id: '',
        record2_id: '',
        metric_name: '',

        columnHeaders: [
        { label: 'Record_Pair',key: 'RecordPair'},
        { label: 'Record1_ID', key: 'record1_id'},
        { label: 'Record2_ID', key: 'record2_id'},
        { label: 'Field_value1', key: 'record1'},
        { label: 'Field_value2', key: 'record2'},
        { label: 'Pair_distance', key: 'pair_distance'}, ],

      filterData: []

        }
    },


 methods:{
             format(percentage) {
            if (percentage < 25){
                return "Great Match"
            }
            else if (percentage < 50) {
                return "Good Match"
            }
            else if (percentage <75){
                return "Bad Match"
            }
            else {
                console.log("percentage > 75" + "," + percentage)
                return "Not Match"
            }},

            customColorMethod(percentage) {

            if (percentage < 25) {
                console.log("<25" + percentage)
                return '#32CD32';
                }

            else if (percentage < 50){
                console.log("<50" +"," + percentage)
                return '#DDD23B';
             }
             else if (percentage < 75) {
                console.log("<75" + "," +percentage)
                return '#E98B2A';
             }
             else{
                console.log("<=100" + "," +percentage)
                return '#FF0000';
                }
             },

             getPairComparison:function () {
                var _this = this;

                _this.RecordPair = _this.$route.query.RecordPair
                _this.record1_id = _this.$route.query.record1_id;
                _this.record2_id = _this.$route.query.record2_id;
                _this.field_value1 = _this.$route.query.field_value1;
                _this.field_value2 = _this.$route.query.field_value2;

                var params = new URLSearchParams()
                params.append('RecordPair', _this.RecordPair)
                params.append('record1_id', _this.record1_id)
                params.append('record2_id', _this.record2_id)
                params.append('field_value1', _this.field_value1)
                params.append('field_value2', _this.field_value2)

                axios.post('http://localhost:8080/PairRecord', params).then(function (resp){
                    _this.filterData = resp.data
                    console.log(_this.filterData)
                })
                
               }
        },

        created:function(){
            this.RecordPair = this.$route.query.RecordPair;
            this.record1_id = this.$route.query.record1_id;
            this.record2_id = this.$route.query.record2_id;
            this.pair_distance = this.$route.query.pair_distance;

            console.log(this.RecordPair, this.record1_id, this.record2_id, this.pair_distance);

            this.getPairComparison();
        }
}

</script>
